[vdl-tab-nav-bar] {
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: row;
  flex-shrink: 0; }

[vdl-tab-link] {
  line-height: 40px;
  height: 40px;
  margin: 0;
  padding: 0 15px;
  font-size: 16px;
  font-family: Roboto, sans-serif;
  font-weight: 500;
  cursor: pointer;
  box-sizing: border-box;
  color: #414142;
  min-width: 160px;
  text-align: center;
  border-radius: 3px;
  transition: border-bottom 4ms ease-in-out 3ms;
  text-decoration: none;
  position: relative;
  overflow: hidden;
  border-radius: 3px;
  margin: 0; }
  [vdl-tab-link]:focus, [vdl-tab-link].focus {
    outline: none; }
  [vdl-tab-link]:hover, [vdl-tab-link].hover {
    background-color: #f3f3f3; }
  [vdl-tab-link].vdl-tab-link-active, [vdl-tab-link].vdl-tab-label-active {
    color: #005a99; }
  [vdl-tab-link].vdl-tab-disabled {
    cursor: default;
    pointer-events: none;
    color: #a5a5a5; }

[vdl-tab-link]:focus {
  outline-offset: -2px;
  outline-width: 0.5px;
  outline-style: auto;
  outline-color: #005a99; }

@media (max-width: 600px) {
  [vdl-tab-link] {
    min-width: 72px; } }

vdl-ink-bar {
  position: absolute;
  bottom: 0;
  height: 2px;
  background-color: #005a99;
  transition: 500ms cubic-bezier(0.35, 0, 0.25, 1); }
  .vdl-tab-group-inverted-header vdl-ink-bar {
    bottom: auto;
    top: 0; }
